//Site defaults
:root {
    --heading-line-height: 1;
}

h1,
.h1 {
    line-height: $h1-line-height-mobile;
    margin-block-start: 0;
    margin-block-end: 1.5rem;
    font-size: $h1-font-size-mobile;
    &:not(:first-child) {
        margin-block-start: 2rem;
    }
    @include media-breakpoint-up(lg) {
        font-size: $h1-font-size;
        line-height: calc(var(--heading-line-height) * 1.3);
        &:not(:first-child) {
            margin-block-start: 4rem;
        }
    }
}

h2,
.h2 {
    line-height: $h2-line-height-mobile;
    margin-block-end: 1.5rem;
    font-size: $h2-font-size-mobile;
    &:not(:first-child) {
        margin-block-start: 2rem;
    }
    @include media-breakpoint-up(lg) {
        font-size: $h2-font-size;
        line-height: calc(var(--heading-line-height) * 1.25);
        &:not(:first-child) {
            margin-block-start: 3rem;
        }
    }
}

h3,
.h3 {
    line-height: calc(var(--heading-line-height) * 1.33);
    margin-block-end: 1.5rem;
    font-size: $h3-font-size;
    &:not(:first-child) {
        margin-block-start: 2rem;
    }
    @include media-breakpoint-up(lg) {
        &:not(:first-child) {
            margin-block-start: 2.25rem;
        }
    }
}

h4,
.h4 {
    line-height: calc(var(--heading-line-height) * 1.2);
    margin-block-end: 1.5rem;
    margin-block-start: 2rem;
    font-size: $h4-font-size;
}

h5,
.h5 {
    line-height: calc(var(--heading-line-height) * 1.25);
    margin-block-end: 1.5rem;
    margin-block-start: 1.25rem;
    font-size: $h5-font-size;
    @include media-breakpoint-up(lg) {
        margin-block-start: 1.5rem;
    }
}

h6,
.h6 {
    line-height: calc(var(--heading-line-height) * 1.14);
    margin-block-end: 1.5rem;
    margin-block-start: 1.25rem;
    font-size: $h6-font-size;
    @include media-breakpoint-up(lg) {
        margin-block-start: 1.5rem;
    }
}
