// QGDS QOL Button
// extends https://getbootstrap.com/docs/5.3/components/buttons/#variables

// QGDS specific button variables.
.btn {
  --#{$prefix}btn-padding-x: 1.5rem;
  --#{$prefix}btn-padding-y: 0.5rem;
  --#{$prefix}btn-font-family: #{$font-family-sans-serif};
  --#{$prefix}btn-border-width: 3px;
  --#{$prefix}btn-active-border-color: transparent;
  --#{$prefix}btn-border-radius: 0.25rem;
  --#{$prefix}btn-focus-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  --#{$prefix}btn-disabled-opacity: 0.5;
  --#{$prefix}btn-spinner-icon-size__md: 1.5rem;
  --#{$prefix}icon-color: var(--qld-action-icon-color);
  --#{$prefix}icon-hover-color: var(--qld-action-icon-hover-color);

  //Primary button variant
  &-primary {
    --#{$prefix}btn-bg: var(--#{$prefix}light-action-primary);
    --#{$prefix}btn-hover-bg: var(--#{$prefix}light-action-primary-hover);
    --#{$prefix}btn-active-bg: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-active-color: var(--#{$prefix}light-text-heading);
    --#{$prefix}btn-disabled-bg: var(--#{$prefix}grey);
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-grey-muted);
    --#{$prefix}btn-disabled-opacity: 1;
    --#{$prefix}btn-disabled-border-color: transparent;
    --#{$prefix}btn-focus-bg: var(--#{$prefix}light-action-primary-hover);
    --#{$prefix}btn-focus-border-color: var(
      --#{$prefix}light-action-primary-hover
    );
    --#{$prefix}icon-color: currentColor;
    --#{$prefix}icon-hover-color: currentColor;
  }
  //Secondary button variant
  &-secondary,
  &-outline-secondary {
    --#{$prefix}btn-bg: transparent;
    --#{$prefix}btn-color: var(--#{$prefix}light-action-primary);
    --#{$prefix}btn-border-color: var(--#{$prefix}light-action-secondary);
    --#{$prefix}btn-hover-border-color: var(
      --#{$prefix}light-action-secondary-hover
    );
    --#{$prefix}btn-hover-color: var(--#{$prefix}light-action-primary);
    --#{$prefix}btn-hover-bg: transparent;
    --#{$prefix}btn-active-color: var(--#{$prefix}light-text-heading);
    --#{$prefix}btn-active-bg: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-active-border-color: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-active-shadow: none;
    --#{$prefix}btn-disabled-bg: transparent;
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-grey-muted);
    --#{$prefix}btn-disabled-opacity: 1;
    --#{$prefix}btn-disabled-border-color: var(--#{$prefix}grey);
    --#{$prefix}gradient: none;
    --#{$prefix}btn-focus-border-color: var(
      --#{$prefix}light-action-secondary-hover
    );
  }

  //Tertiary button variant
  &-tertiary {
    --#{$prefix}btn-color: #{$primary};
    --#{$prefix}btn-border-color: transparent;
    --#{$prefix}btn-hover-color: #{$primary};
    --#{$prefix}btn-hover-bg: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-hover-border-color: transparent;
    --#{$prefix}btn-active-color: var(--#{$prefix}light-text-heading);
    --#{$prefix}btn-active-bg: transparent;
    --#{$prefix}btn-active-shadow: none;
    --#{$prefix}btn-disabled-bg: transparent;
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-grey-muted);
    --#{$prefix}btn-disabled-opacity: 1;
    --#{$prefix}btn-disabled-border-color: transparent;
    --#{$prefix}gradient: none;
  }
}

.dark .btn,
.dark-alt .btn {
  //Primary button variant (dark mode)
  &-primary {
    --#{$prefix}btn-bg: var(--#{$prefix}dark-action-secondary);
    --#{$prefix}btn-border-color: var(--#{$prefix}dark-action-secondary);
    --#{$prefix}btn-color: var(--#{$prefix}text-darkest);
    --#{$prefix}btn-hover-color: var(--#{$prefix}text-darkest);
    --#{$prefix}btn-hover-bg: var(--#{$prefix}dark-action-primary-hover);
    --#{$prefix}btn-hover-border-color: var(
      --#{$prefix}dark-action-primary-hover
    );
    --#{$prefix}btn-active-color: var(--#{$prefix}light-text-heading);
    --#{$prefix}btn-active-bg: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-disabled-bg: rgba(224, 224, 224, 0.1);
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-text-lighter);
    --#{$prefix}btn-disabled-border-color: transparent;
    --#{$prefix}btn-focus-bg: var(--#{$prefix}dark-action-primary-hover);
    --#{$prefix}btn-focus-border-color: var(
      --#{$prefix}dark-action-primary-hover
    );
  }

  //Secondary button variant (dark mode)
  &-secondary,
  &-outline-secondary {
    --#{$prefix}btn-border-color: var(--#{$prefix}dark-action-secondary);
    --#{$prefix}btn-color: var(--#{$prefix}white);
    --#{$prefix}btn-hover-border-color: var(
      --#{$prefix}dark-action-secondary-hover
    );
    --#{$prefix}btn-hover-bg: transparent;
    --#{$prefix}btn-hover-color: var(--#{$prefix}white);
    --#{$prefix}btn-active-bg: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-active-border-color: var(--#{$prefix}extra-light-grey);
    --#{$prefix}btn-active-color: var(--#{$prefix}light-text-heading);
    --#{$prefix}btn-disabled-bg: transparent;
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-text-lighter);
    --#{$prefix}btn-disabled-border-color: rgba(224, 224, 224, 0.1);
    --#{$prefix}btn-focus-bg: transparent;
    --#{$prefix}btn-focus-border-color: var(
      --#{$prefix}dark-action-secondary-hover
    );
  }

  //Tertiary button variant (dark mode)
  &-tertiary {
    --#{$prefix}btn-color: var(--#{$prefix}white);
    --#{$prefix}btn-bg: transparent;
    --#{$prefix}btn-hover-color: var(--#{$prefix}white);
    --#{$prefix}btn-hover-bg: var(--#{$prefix}dark-background-shade);
    --#{$prefix}btn-active-bg: transparent;
    --#{$prefix}btn-active-border-color: transparent;
    --#{$prefix}btn-active-color: var(--#{$prefix}white);
    --#{$prefix}btn-focus-bg: var(--#{$prefix}dark-background-shade);
    --#{$prefix}btn-disabled-bg: transparent;
    --#{$prefix}btn-disabled-color: var(--#{$prefix}dark-text-lighter);
    --#{$prefix}btn-disabled-border-color: transparent;
  }
}

// General styling rules.
a.btn,
.btn {
  --qld-body-color: var(--#{$prefix}btn-color);
  white-space: normal;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  min-width: 10rem;
  min-height: 3.25rem;
  text-align: center;

  .qld-icon {
    flex-shrink: 0;
  }

  span[class^="icon-"],
  span[class^="fa-"] {
    margin: 0 0.5rem;
  }

  &:hover {
    --#{$prefix}icon-color: var(--#{$prefix}icon-hover-color);
  }

  &:hover,
  &:visited,
  &:focus {
    color: var(--#{$prefix}btn-color);
    text-decoration: underline;
    text-decoration-color: inherit;
    text-underline-offset: 0.3em;
  }

  &:active {
    color: var(--#{$prefix}btn-active-color);
  }

  &:focus {
    outline: var(--#{$prefix}light-blue) solid 3px;
    outline-offset: 2px;
  }

  &:visited {
    color: var(--#{$prefix}btn-color);

    &:hover {
      text-decoration-color: inherit;
    }
  }

  &:disabled,
  &.disabled {
    --qld-body-color: var(--#{$prefix}btn-disabled-color);
    --#{$prefix}icon-color: currentColor;

    &:visited {
      color: var(--#{$prefix}btn-disabled-color);
    }

    &:focus {
      background-color: var(--#{$prefix}btn-disabled-bg) !important;
      border-color: var(--#{$prefix}btn-disabled-border-color) !important;
      color: var(--#{$prefix}btn-disabled-color) !important;
      box-shadow: none !important;
      text-decoration: none !important;
    }
  }

  // Progress state styles
  &.btn-progress {
    cursor: not-allowed;
    pointer-events: none;

    .spinner-border {
      width: var(--#{$prefix}btn-spinner-icon-size__md);
      height: var(--#{$prefix}btn-spinner-icon-size__md);
      --qld-spinner-border-width: 0.094rem;
    }

    .btn-label-default {
      display: none;
    }

    .btn-label-progress {
      display: inline;
    }

    // Maintain focus styles for screen readers
    &:focus {
      outline: var(--#{$prefix}light-blue) solid 3px;
      outline-offset: 2px;
    }

    // Remove hover effects
    &:hover {
      text-decoration: none;
    }
  }

  // Default state - hide progress label
  .btn-label-progress {
    display: none;
  }

  .btn-label-default {
    display: inline;
  }

  //Primary button variant
  &-primary {
    box-shadow:
      0px 4px 8px 3px rgba(0, 0, 0, 0.1),
      0px 1px 3px rgba(0, 0, 0, 0.2);

    &:hover {
      box-shadow:
        7px 6px 13px rgba(0, 0, 0, 0.02),
        0px 2px 5px rgba(0, 0, 0, 0.1),
        0px 13px 20px rgba(0, 0, 0, 0.1);
    }

    &:active {
      box-shadow:
        0px 1px 2px rgba(0, 0, 0, 0.2),
        0px 1px 3px 1px rgba(0, 0, 0, 0.1);
      text-decoration: none;
    }

    &:focus {
      box-shadow:
        0px 1px 2px rgba(0, 0, 0, 0.3),
        0px 2px 6px 2px rgba(0, 0, 0, 0.15);
      background-color: var(--#{$prefix}btn-focus-bg);
      border-color: var(--#{$prefix}btn-focus-border-color);
    }

    &:disabled,
    &.disabled {
      box-shadow: none;
    }
  }

  //Secondary button variant
  &-secondary,
  &-outline-secondary {
    --qld-body-color: var(--#{$prefix}btn-border-color);
    &:hover {
      --qld-body-color: var(--#{$prefix}btn-hover-border-color);
    }

    &:focus {
      text-decoration: underline;
      text-underline-offset: 0.3em;
      border-color: var(--#{$prefix}btn-focus-border-color);
      --qld-body-color: var(--#{$prefix}btn-hover-border-color);
    }

    &:active {
      text-decoration: none;
      --qld-body-color: var(--#{$prefix}btn-active-color);
    }
  }

  //Tertiary button variant
  &-tertiary {
    text-decoration: underline;
    text-underline-offset: 0.3em;
    --qld-body-color: var(--#{$prefix}light-link);

    &:hover {
      --qld-body-color: var(--#{$prefix}light-action-secondary-hover);
    }

    &:focus {
      background-color: var(--#{$prefix}btn-hover-bg);
      --qld-body-color: var(--#{$prefix}light-action-secondary-hover);
    }

    &:active {
      text-decoration: none;
      color: var(--#{$prefix}btn-active-color);
      --qld-body-color: var(--#{$prefix}btn-active-color);
    }

    &:disabled,
    &.disabled {
      text-decoration: none;
    }
  }
}

// Dark mode button variants
.dark .btn,
.dark a.btn,
.dark-alt .btn,
.dark-alt a.btn {
  &:disabled,
  &.disabled {
    --qld-body-color: var(--#{$prefix}btn-disabled-color);

    &:focus {
      background-color: var(--#{$prefix}btn-disabled-bg) !important;
      border-color: var(--#{$prefix}btn-disabled-border-color) !important;
      color: var(--#{$prefix}btn-disabled-color) !important;
      box-shadow: none !important;
      text-decoration: none !important;
    }
  }

  &:focus {
    outline: var(--#{$prefix}dark-focus) solid 3px;
    outline-offset: 2px;
  }

  // Progress state focus for dark mode
  &.btn-progress:focus {
    outline: var(--#{$prefix}dark-focus) solid 3px;
    outline-offset: 2px;
  }

  &-primary {
    &:focus {
      background-color: var(--#{$prefix}btn-focus-bg);
      border-color: var(--#{$prefix}btn-focus-border-color);
    }
  }

  &-secondary,
  &-outline-secondary {
    --qld-body-color: var(--#{$prefix}btn-border-color);

    &:hover {
      border: var(--#{$prefix}btn-border-width) solid
        var(--#{$prefix}btn-hover-border-color);
      --qld-body-color: var(--#{$prefix}btn-hover-border-color);
    }

    &:focus {
      border-color: var(--#{$prefix}btn-focus-border-color);
      --qld-body-color: var(--#{$prefix}btn-focus-border-color);
    }

    &:active {
      color: var(--#{$prefix}btn-active-color);
      background-color: var(--#{$prefix}btn-active-bg);
      border-color: var(--#{$prefix}btn-active-bg);
      --qld-body-color: var(--#{$prefix}btn-active-color);
    }
  }

  &-tertiary {
    --qld-body-color: var(--#{$prefix}dark-link);

    &:hover {
      --qld-body-color: var(--#{$prefix}dark-action-secondary-hover);
    }

    &:focus {
      background-color: var(--#{$prefix}btn-hover-bg);
    }

    &:active {
      text-decoration: none;
      --qld-body-color: var(--#{$prefix}dark-link);
    }
  }
}

// Spinner animation
@keyframes btn-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
