// Generate CSS custom properties for qld-tokens & project colours on :root to prevent utility class generation
:root {
  @include m.generate-color-vars($qld-tokens);
  @include m.generate-color-vars($project-colors);
}

// These variables replace existing out-of-the-box bootstrap variables defined under /node_modules/bootstrap/.../_variables.css`

$project-base-colors: (
  //Legacy color based variables - to review
  "primary": $color-default-color-light-action-primary,
  "primary-dark": $color-default-color-dark-action-primary,
  "secondary": $color-default-color-light-action-secondary,
  "success": $qld-notify-success,
  "info": $qld-notify-information,
  "warning": $qld-notify-alert,
  "danger": $qld-notify-warning,
  "light": $qld-extra-light-grey,
  "dark": $qld-dark-grey,
  "dark-text": $qld-dark-text,
  "dark-border": $qld-dark-border,
  "dark-alt-border": $qld-dark-alt-border,
  "alt-button-hover": $qld-alt-button-hover,
  "dark-blue-shade": $qld-dark-blue-shade,
  "dark-focus": $qld-dark-focus,
  "light-focus": $qld-light-focus,
  "text-light-blue-dark": $qld-text-light-blue-dark,
  "light-green-dark": $qld-light-green-dark,
  "dark-green-dark": $qld-dark-green-dark,
  "light-yellow": $qld-light-yellow,
  "button-dark-blue": $qld-button-dark-blue,
  "text-darkest": $qld-text-darkest,
  "dark-hover": $qld-dark-hover
);

$theme-colors: map-merge($theme-colors, $project-base-colors);

// DO NOT override the variables which is predefined in bootstrap;
// Cusotm GRID breakpoints: https://getbootstrap.com/docs/5.2/layout/grid/#variables
$grid-breakpoints: (
  xs: 0,
  sm: 400px,
  md: 700px,
  lg: 992px,
  xl: 1312px,
);

// Map merge to include custom breakpoints
$breakpoints: map-merge($grid-breakpoints, ());

// Gutters inside rows (column gutters)
$grid-gutter-width: 2rem;

// Default border radius
$border-radius: 0.25rem;
$border-radius-sm: 0.25rem;
$border-radius-lg: 0.25rem;

// QGOV Borders
$qld-border-width-thin: 1px;
$qld-border-width-default: 2px;
$qld-border-width-medium: 3px;
$qld-border-width-thick: 4px;
$qld-border-width-extra-thick: 8px;

// DO NOT override the variables which is predefined in bootstrap;
// Extended spacers to procude 8px and 16px increments
// use px-16, py-sm-16, ml-40, mr-40, etc
$qld-custom-spacers: (
  12: (
    $spacer * 0.75,
  ),
  16: $spacer,
  20: (
    $spacer * 1.25,
  ),
  24: (
    $spacer * 1.5,
  ),
  32: (
    $spacer * 2,
  ),
  40: (
    $spacer * 2.5,
  ),
  48: (
    $spacer * 3,
  ),
  56: (
    $spacer * 3.5,
  ),
  64: (
    $spacer * 4,
  ),
  72: (
    $spacer * 4.5,
  ),
  80: (
    $spacer * 5,
  ),
);

$spacers: map-merge($spacers, $qld-custom-spacers);
